import React, { Component } from 'react';
import './Client.css';
class Client extends Component{
    render(){
        return(
            <React.Fragment>

		<div class="client-word">
			<h5 >A WORD FROM OUR CLIENTS</h5>

		</div>

		<div class="client-word">
			<marquee direction="right" class="m-tag m-top m-tagTB"  onmouseover="this.stop();" onmouseout="this.start();"><ul>
<li>SMART</li>
<li>Managing Partner</li>
<li>INTERACTIVE</li>
<li>ACCOMMODATING</li>
<li>TOP NOTCH</li>
<li>ADAPTABLE</li>
<li>ORGANIZED</li>
<li>KINDRED SPIRITS</li>
</ul></marquee>
		</div>

		<div class="client-word">
			<marquee direction="left" class="m-tag m-middle" onmouseover="this.stop();" onmouseout="this.start();"><ul>
<li>COLLABORATIVE</li>
<li>ABOVE AND BEYOND</li>
<li>EXCELLENT </li>
<li>KNOWLEDGEABLE</li>
<li>CUSTOM</li>
<li>EXPERTS</li>
<li>FANTASTIC</li>
<li>THE BEST</li>
<li>QUALITY</li>
<li>PATIENT</li>
<li>DILIGENT</li>
</ul></marquee>
		</div>

		<div class="client-word">
			<marquee direction="right" class="m-tag m-bottom m-tagTB" onmouseover="this.stop();" onmouseout="this.start();"><ul>
<li>TRUST</li>
<li>UNIQUE</li>
<li>AVAILABLE </li>
<li>COMMITMENT </li>
<li>PASSION</li>
<li>IN-DEPTH</li>
<li>REFRESHING</li>
<li>FLEXIBILITY</li>
<li>COMMUNICATIVE</li>
<li>PERSONABLE</li>
<li>SMOOTH</li>
<li>EFFECTIVE</li>
</ul></marquee>
		</div>


		<div class="client-word">
			<p><a class="contact" href="https://echoboomerdesign.com/contact/">CONTACT US</a></p>

		</div>
        </React.Fragment>

        );
    }
}
export default Client;


