import React,{Component} from 'react';
import './SectionOne.css';

class SectionOne extends Component{
    render(){
        return(
			<React.Fragment>
<section className="container">
<div className="lucky_col">
	<div className="txt-col"></div>
	<div className="txt-col">
<h1 className="head-1">LUCKILY WE HAVE A FEW SCREWS LOOSE.</h1>
<img className="alignnone wp-image-6871 size-full" src="/images/zig-zag-separator-white-1.png"/>
<p className = "para-1">That doesnt mean we charge a fortune to throw fruit at a wall and call it art. It just means were happy to be passionate oddballs who make great stuff.</p>
<p><a className="work" href="http://gosoftinformatics.com/ebd/our-work">WORK WITH US</a></p>
</div></div>


		<div className="wpb_wrapper">
			<img src="/images/final-thumb.gif" className="thumb"/>
		</div>

	<div className="image_1">
				<img src="/images/plus-img.png" className="vc_single_image-img attachment-full"/>
		</div>
		</section>
</React.Fragment>

);
}
}
export default SectionOne;



