import React, { Component } from 'react';
import './Newbtn.css';

class Newsbtn extends Component{
    render(){
        return(
            <div class="only-for-button news-button">
                <div>
             <a href="https://echoboomerdesign.com/news/" 
             class="edgtf-btn edgtf-btn-large edgtf-btn-solid edgtf-btn-custom-hover-bg blog-button" 
             data-hover-bg-color="#ffffff">
			<span class="edgtf-btn-text">More News</span>
	    </a>
        </div>
        </div>

        );
    }
}
export default Newsbtn;


