import React, { Component } from 'react';

import SectionOne from './Component/SectionOne/SectionOne';
import SectionTwo from './Component/SectionTwo/SectionTwo';
import OurWork from './Component/OurWork/OurWork';
import Together from './Component/Together/Together';
import Client from './Component/Client/Client';
import OurTeam from './Component/OurTeam/OurTeam';
import OurNews from './Component/OurNews/OurNews';
import Quote from './Component/Quote/Quote';
import Workbtn from './Component/Workbtn/Workbtn';
import Newsbtn from './Component/Newsbtn/Newsbtn';
import Header from './Component/Header/Header';
import Footer from './Component/Footer/Footer';

class App extends Component {
  render() {
    return (
      <React.Fragment>
        <Header/>
<SectionOne/>
<SectionTwo/>
<OurWork/>
<Workbtn/>
<Together/>
<OurTeam/>
<Client/>
<OurNews/>
<Newsbtn/>
<Quote/>

<Footer/>

</React.Fragment>
    );
  }
}

export default App;



