import React, { Component } from 'react';
import './Together.css';

class Together extends Component{
    render(){
        return(
            <React.Fragment>
                <section className="container-4">
            <div class="lets_work">
       

			<h4>LETS WORK TOGETHER</h4>
<h2>We collaborate with ambitious brands and people.</h2>
<p><a class="touch" href="https://echoboomerdesign.com/contact/">GET IN TOUCH</a></p>

		</div>
 
        <div class="image_3">
          
        <img src="/images/plus-img.png"/>
            
        </div>
        </section>
    </React.Fragment>

        );
    }
}
export default Together;


