import React, { Component } from 'react';
import './Workbtn.css';

class Workbtn extends Component{
    render(){
        return(
            <div class=" only-for-button view_more_btn" >
                            <a href=""
                            class="edgtf-btn edgtf-btn-large edgtf-btn-solid edgtf-btn-custom-hover-bg our-work-button" 
                            data-hover-bg-color="#ffffff">
			<span class="edgtf-btn-text">View More</span>
	    </a></div>
        );
    }
}
export default Workbtn;


